import PortExcel from '../../Excel/Ports.xlsx'
import readXlsxFile from "read-excel-file";

export class ListOfPorts {
    constructor() {
        if (!this.listOfPorts) this.listOfPorts = [];
        if (!ListOfPorts._instance) {
            ListOfPorts._instance = this;
        }
        let instance = ListOfPorts._instance;
        instance.readExcel();
        return ListOfPorts._instance;
    }

    readExcel = () => {
        readXlsxFile(PortExcel).then((rows) => {
            console.log("FUCKING PORTS ROWS:", rows);
            this.listOfPorts = rows;
        });
    }


    static getInstance() {
        return this._instance;
    }
}

export default [];